<template>
	<view>
		<form>
			<view class="cu-bar bg-white margin-top">
				<view class="title">班课封面</view>
				<view class="action">
					{{imgList.length}}/4
				</view>
			</view>
			<view class="cu-form-group">
				<view class="grid col-4 grid-square flex-sub">
					<view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
					 <image :src="imgList[index]" mode="aspectFill"></image>
						<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
							<text class='cuIcon-close'></text>
						</view>
					</view>
					<view class="solids" @tap="ChooseImage" v-if="imgList.length<4">
						<text class='cuIcon-cameraadd'></text>
					</view>
				</view>
			</view>
			<view class="cu-form-group">
				<view class="title">班级</view>
				<input placeholder="请输入" name="class"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">课程</view>
				<input placeholder="请输入" name="course"></input>
			</view>
			
			<view class="cu-form-group margin-top">
				<view class="title">学期</view>
				<picker @change="PickerChange" :value="index" :range="picker">
					<view class="picker">
						{{index>-1?picker[index]:picker[1]}}
					</view>
				</picker>
			</view>

			<view class="cu-form-group">
				<view class="title">学校院系</view>
				<picker mode="multiSelector" @change="MultiChange" @columnchange="MultiColumnChange" :value="multiIndex" :range="multiArray">
					<view class="picker">
						{{multiArray[0][multiIndex[0]]}}，{{multiArray[1][multiIndex[1]]}}
					</view>
				</picker>
			</view>
			<view class="cu-form-group">
				<view class="title">考试安排</view>
				<picker mode="date" :value="date" start="2020-09-01" end="2021-09-01" @change="DateChange">
					<view class="picker">
						{{date}}
					</view>
				</picker>
			</view>
		</form>
		<view >
			<button @click="addCourse()">创建班课</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				index: -1,
				picker: ['2020-2021-1', '2020-2021-2', '2021-2022-1', '2021-2022-2'],
				multiArray: [
					['福州大学', '福建师范大学'],
					['数学与计算机科学学院', '物信学院', '经管学院', '外语学院'],
				],
				objectMultiArray: [
					[{
							id: 0,
							name: '福州大学'
						},
						{
							id: 1,
							name: '福建师范大学'
						}
					],
					[{
							id: 0,
							name: '数学与计算机科学学院'
						},
						{
							id: 1,
							name: '物信学院'
						},
						{
							id: 2,
							name: '经管学院'
						},
						{
							id: 3,
							name: '外语学院'
						}
					]
				],
				multiIndex: [0, 0, 0],
				date: '2018-12-25',
				imgList: [],
				modalName: null
			};
		},
		methods: {
			addCourse() {
				uni.redirectTo({
					url:"./course"
				})
			},
			PickerChange(e) {
				this.index = e.detail.value
			},
			MultiChange(e) {
				this.multiIndex = e.detail.value
			},
			MultiColumnChange(e) {
				let data = {
					multiArray: this.multiArray,
					multiIndex: this.multiIndex
				};
				data.multiIndex[e.detail.column] = e.detail.value;
				switch (e.detail.column) {
					case 0:
						data.multiArray[1] = ['数学与计算机科学学院', '物信学院', '经管学院', '外语学院'];
						data.multiIndex[1] = 0;
						data.multiIndex[2] = 0;
						break;
				}
				this.multiArray = data.multiArray;
				this.multiIndex = data.multiIndex;
			},
			ChooseImage() {
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}
					}
				});
			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '召唤师',
					content: '确定要删除这段回忆吗？',
					cancelText: '再看看',
					confirmText: '再见',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
		}
	}
</script>

<style>
	@import "/checkui/main.css";
	@import "/checkui/icon.css";
	.cu-form-group .title {
		min-width: calc(4em + 15px);
	}
</style>
